export default {
	props: {
		orderItemInfo: {
			type: Object,
			default() {
				return {}
			}
		}
	},
	data () {
		return 	{
			// 该订单的商品总数
			OrderFoodsCount: 0,
			// 该订单的总金额
			OrderFoodsSunMoney: 0
		}
	},
	created() {
		this.resFoodsCount()
	},
	methods: {
		
		// 计算商品总数
		resFoodsCount() {
			let count = 0;
			for (let i = 0; i<this.orderItemInfo.foodList.length; i++) {
					count += parseInt(this.orderItemInfo.foodList[i].foodCount)
			}
			this.OrderFoodsCount = count
		},
		// 计算商品金额
		resSumMoney() {
			
		},
		
		// 跳转到订单详情页
		openOrderDetail() {
			let orderId = 12
			// 将本订单ID传过去
			uni.navigateTo({
				url: '../../pages/order_detail/order_detail?orderId=' + orderId,
				complete (res) {
					console.log(res)
				}
			})
		}
	},
	computed: {
		
	}
}
